import EmployeeNav from "./EmployeeNav";
import React, { useState } from 'react';
import { fetchSubmitStudentList } from "../../functions/employee";

const csvIcon = new URL("../images/csvIcon.png", import.meta.url)
function InsertUsers() {
    const [file, setFile] = useState(null);
    const [uploadText, setUploadText] = useState("Click to Upload CSV File");

    function handleFileChange(event) {
        setFile(event.target.files[0]);
        setUploadText(event.target.files[0].name);
        console.log()
    }

    async function handleSubmit(event) {
        event.preventDefault();
        
        try {
            const data = await fetchSubmitStudentList(file);
            console.log(data);
        } catch(error) {
            console.error(error);
        }
    }
        

    return ( 
    <>
        <div className="screenLayout">
            <EmployeeNav />
            <div className="title_contentDiv">
                <span className="page-title"> Student Account Creation </span>
                <div className="uploadContainer">

                    <div className='innerDivider'>
                        <label className="uploadDiv">
                                <img src={csvIcon}/>
                                <p>{uploadText}</p>
                                <input className="uploadFile" type="file" onChange={handleFileChange}/>  
                        </label>
                    <button className="uploadButton" type="submit" onClick={handleSubmit}>Upload</button>
                    </div>


                </div>
            </div>
        </div>
        
    </> );
}

export default InsertUsers;