import React from "react";
import './style.css';
import { useNavigate } from "react-router-dom";
import Main from './Main'


const ForgotPass = () => {

    const pupLOGO = new URL('./images/PUPLOGO.png', import.meta.url)
    const navigate = useNavigate();

    return (
        <>
            <div className="screenLayout">
                <div className="header">
                    <div className="logo_textContainer">
                        <img src={pupLOGO} className="headerLogo" />
                        <span className="headerText">Student Deficiency Portal</span>
                    </div>
                </div>

                <div className="forgot_pass_div">
                    <span className="forgot_pass_span">You forgot your password?</span>
                    <span className="forgot_pass_span1">You can easily request a new password here.</span>
                    <input className="forgot_pass_input"></input>
                    <button className="forgot_pass_button">Request New Password</button>
                    <span className="forgot_pass_signIN" onClick={() => { navigate('/'); }}> Sign In</span>
                </div>

            </div>
        </>
    )
}

export default ForgotPass;